:root {
  --color-white: #fff;
  --color-light: #fafafa;
  --color-lightgray: #1111111a;
  --color-gray: #111111b2;
  --color-dark: #1e1823;
  --color-black: #111;
  --color-orange: #fd9222;
  --color-lightorange: #fcf1e0;
}

body {
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.container {
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

button {
  cursor: pointer;
}

.second-heading {
  letter-spacing: -.03em;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 1.2;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.header-container {
  padding: 0 32px;
}

.header-navigation {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.header-logo > a {
  align-items: center;
  gap: 4px;
  padding: 31px 0;
  display: flex;
}

.header-chocolate-icon {
  fill: #0000;
  stroke: #fd9222;
}

.header-nav-box > ul {
  gap: 24px;
  display: none;
}

.header-logo-text {
  color: #111;
  letter-spacing: -.54px;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 20px;
}

.header-nav-list-item {
  letter-spacing: -.28px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

.header-social-media-box > ul {
  gap: 18px;
  display: none;
}

.header-burger-menu {
  display: block;
}

.header-burger-button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 4px;
}

.header-burger-icon {
  fill: #0000;
  stroke: #111;
  stroke-width: 2px;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .header-container {
    padding: 0 32px;
  }

  .header-navigation {
    max-width: 704px;
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .header-burger-icon {
    fill: #0000;
    stroke: #111;
    stroke-width: 2.5px;
  }
}

@media screen and (min-width: 1200px) {
  .header-container {
    width: 1136px;
    margin: 0 auto;
    padding: 0 32px;
  }

  .header-logo-text {
    white-space: nowrap;
  }

  .header-burger-menu {
    display: none;
  }

  .header-nav-box {
    margin-left: 188px;
  }

  .header-nav-box > ul {
    gap: 24px;
    display: flex;
  }

  .header-social-media-box {
    margin-left: 137px;
  }

  .header-social-media-box > ul {
    gap: 18px;
    display: flex;
  }

  .header-nav-list-item {
    white-space: nowrap;
  }

  .header-navigation {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .header-twitter-icon {
    fill: #0000;
    stroke: #111;
    transition-property: stroke;
    transition-duration: .25s;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  }

  .header-twitter-icon:hover, .header-twitter-icon:focus {
    stroke: #fd9222;
  }

  .header-instagram-icon {
    fill: #0000;
    stroke: #111;
    transition-property: stroke;
    transition-duration: .25s;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  }

  .header-instagram-icon:hover, .header-instagram-icon:focus {
    stroke: #fd9222;
  }
}

.hero-background {
  color: var(--color-white);
  height: 621px;
  background-image: linear-gradient(#1110, #111), url("hero-background-mobile.3fe8c82d.jpg");
  background-repeat: no-repeat;
  border-radius: 30px;
  flex-wrap: wrap;
  align-items: end;
  margin-bottom: 71px;
  padding: 21px 20px;
  display: flex;
}

.hero-heading {
  max-width: 290px;
  text-transform: none;
  margin-bottom: 32px;
  font-weight: 500;
}

.hero-buttons {
  flex-wrap: wrap;
  gap: 32px;
  display: flex;
}

.hero-button-row {
  align-items: center;
  gap: 14px;
  display: flex;
}

.hero-button-row:last-child {
  gap: 12px;
}

.hero-button {
  color: var(--color-white);
  border-radius: 100px;
  padding: 12px 28px;
  font-size: 14px;
  font-weight: 600;
}

.hero-buy {
  background-color: var(--color-orange);
  border: none;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.hero-buy:hover, .hero-buy:focus {
  background-color: var(--color-lightorange);
  color: var(--color-orange);
}

.hero-how {
  border: 1px solid var(--color-white);
  background: none;
}

.hero-how:hover, .hero-how:focus {
  border-color: var(--color-orange);
  color: var(--color-orange);
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.hero-p {
  letter-spacing: -.03em;
  font-size: 12px;
  line-height: 1.17;
}

.hero-scroll {
  background-color: var(--color-white);
  height: 28px;
  width: 28px;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.hero-scroll:hover, .hero-scroll:focus {
  background-color: var(--color-orange);
}

.hero-scroll:hover .hero-scroll-icon, .hero-scroll:focus .hero-scroll-icon {
  fill: var(--color-white);
}

.hero-scroll-icon {
  fill: var(--color-orange);
  width: 16px;
  height: 16px;
}

